import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';

import { AuthService, CustomValidators, ILoginRequest, MessagingService, PaymentService } from '@aft-core';
import { LoginService } from '@scouting/public/services/login.service';
import { generateFullPublicModuleRoute, PUBLIC_MODULE_ROUTES } from '../../../../public.routes';

@Component({
  selector: 'aft-scout-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit, OnDestroy {
  form: FormGroup;
  showPassword = false;
  showResetPasswordMessage = false;
  showPaymentMessage = false;
  private _subscriptions: Array<Subscription> = [];

  constructor(
    private _fb: FormBuilder,
    private _router: Router,
    private _authService: AuthService,
    private _messagingService: MessagingService,
    private _paymentService: PaymentService,
    private _loginService: LoginService
  ) {
    this.form = this._fb.group({
      email: ['', [Validators.required, CustomValidators.Email]],
      password: ['', Validators.required],
    });
  }

  ngOnInit(): void {
    this._subscriptions.push(
      this._paymentService.paymentSuccessful$.subscribe((paymentSuccessful: boolean | undefined) => {
        if (paymentSuccessful) {
          this.showPaymentMessage = paymentSuccessful;
        }
      }),
      this._authService.hasChangedPassword$.subscribe(show => {
        this.showResetPasswordMessage = show;
      })
    );
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(sub => sub.unsubscribe());
  }

  get emailCtrl() {
    return this.form.controls.email;
  }

  get passCtrl() {
    return this.form.controls.password;
  }

  onBlur() {
    this.form.controls.email.markAsDirty();
    this.form.controls.password.markAsDirty();
  }

  onSubmit() {
    if (this.form.valid) {
      this.loginUser(this.form.value);
    } else if (this.form.pristine) {
      this._messagingService.error('LOGIN.TOAST.EMPTY.TITLE', 'LOGIN.TOAST.EMPTY.MESSAGE');
    } else {
      this._messagingService.info('LOGIN.TOAST.INFO.TITLE', 'LOGIN.TOAST.INFO.MESSAGE');
    }
  }

  loginUser(loginRequest: ILoginRequest) {
    // Tries to login, checks if user exists and if it has pending payment then it redirects to payment, else it redirects to home
    this._loginService.checkPayment(loginRequest).subscribe((shouldPay: boolean | undefined) => {
      if (shouldPay) {
        this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER_PAYMENT));
      }
    });
  }

  goToForgot() {
    this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.FORGOT_PASSWORD));
  }

  goToRegister() {
    this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER));
  }
}
