import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';

import { AuthService, CustomValidators, MessagingService } from '@aft-core';
import { generateFullPublicModuleRoute, PUBLIC_MODULE_ROUTES } from '../../../../public.routes';

@Component({
  selector: 'aft-scout-forgot',
  templateUrl: './forgot.component.html',
  styleUrls: ['./forgot.component.scss'],
})
export class ForgotComponent {
  form: FormGroup;
  showPassword = false;

  constructor(
    private _fb: FormBuilder,
    private _router: Router,
    private _authService: AuthService,
    private _translateService: TranslateService,
    private _messagingService: MessagingService
  ) {
    this.form = this._fb.group({
      email: ['', [Validators.required, CustomValidators.Email]],
    });
  }

  get emailCtrl() {
    return this.form.controls.email;
  }

  onBlur(controlName: string) {
    this.form.get(controlName)?.markAsDirty();
  }

  onSubmit() {
    if (this.form.valid) {
      const formValues = this.form.value;
      this._authService.sendForgotEmail({ email: formValues.email, language: this._translateService.currentLang });
    } else {
      this._messagingService.info('FORGOT.TOAST.INFO.TITLE', 'FORGOT.TOAST.INFO.MESSAGE');
    }
  }

  goToLogin() {
    this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.LOGIN));
  }
}
