import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';

import { AppConfigService, APP_ROUTES, MessagingService, PaymentService } from '@aft-core';
import { IRegisteringUser } from '@scouting-public/interfaces/register';
import { generateFullPublicModuleRoute, PUBLIC_MODULE_ROUTES } from '../../../../public.routes';
import { RegisterUserService } from '../../../../services/register-user.service';

@Component({
  selector: 'aft-scout-register-fourth-step',
  templateUrl: './register-payment.component.html',
  styleUrls: ['./register-payment.component.scss'],
})
export class RegisterPaymentComponent implements OnInit, OnDestroy {
  user: IRegisteringUser;
  amountValue: string;
  isInvite: boolean;
  private _subscriptions: Array<Subscription> = [];

  constructor(
    private _router: Router,
    private _registerUserService: RegisterUserService,
    private _configService: AppConfigService,
    private _paymentService: PaymentService,
    private _messagingService: MessagingService
  ) {
    this.user = this._registerUserService.user;
    this.isInvite = this._registerUserService.isInviteRegister$.value;
    this.amountValue = this._configService.envConfig.paypalAmount || '5000';
  }
  ngOnInit(): void {
    if (this.user.firstName === '' || this.user.email === '' || this.user.lastName === '') {
      this._router.navigate(APP_ROUTES.LANDING);
    }

    this._subscriptions.push(
      this._paymentService.paymentSuccessful$.subscribe((paymentSuccessful: boolean | undefined) => {
        if (paymentSuccessful !== undefined) {
          if (paymentSuccessful) {
            this.confirmPayment();
          } else {
            this._messagingService.error('REGISTER.FOURTH_STEP.TOAST.ERROR.TITLE', 'REGISTER.FOURTH_STEP.TOAST.ERROR.MESSAGE');
          }
        }
      })
    );
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(sub => sub.unsubscribe());
  }

  confirmPayment(): void {
    this._registerUserService.resetRegistration();
    this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.LOGIN));
  }

  confirmInvite(): void {
    this._registerUserService.resetRegistration();
    this._router.navigate(APP_ROUTES.LANDING);
  }
}
