import { Component, Input } from '@angular/core';
import { GoogleLoginProvider, SocialAuthService, SocialUser } from 'angularx-social-login';
import { Router } from '@angular/router';

import { AuthService, MessagingService } from '@aft-core';

import { RegisterUserService } from '@scouting-public/services/register-user.service';
import { LoginService } from '@scouting-public/services/login.service';
import { IGoogleLoginRequest, IGoogleSignInResponse } from '@scouting-public/interfaces/google';
import { PROVIDER_TYPES } from '@scouting-public/enums/provider-types';
import { generateFullPublicModuleRoute, PUBLIC_MODULE_ROUTES } from '@scouting-public/public.routes';

@Component({
  selector: 'aft-scout-google-button',
  templateUrl: './google-button.component.html',
  styleUrls: ['./google-button.component.scss'],
})
export class GoogleButtonComponent {
  @Input() register = false;
  @Input() disabled = false;

  private _user: SocialUser = new SocialUser();

  constructor(
    private _socialAuthService: SocialAuthService,
    private _registerUserService: RegisterUserService,
    private _loginService: LoginService,
    private _router: Router,
    private _messagingService: MessagingService,
    private _authService: AuthService
  ) {}

  // Logs with google, checks if user exists and logs user in or redirects to register
  async signInWithGoogle() {
    if (!this.disabled) {
      this._user = await this._socialAuthService.signIn(GoogleLoginProvider.PROVIDER_ID);
      // User logged in to google, log in into system
      const body: IGoogleLoginRequest = {
        email: this._user.email,
        providerId: this._user.id,
        provider: PROVIDER_TYPES.GOOGLE,
      };

      this._loginService.googleLogin(body).subscribe((result: IGoogleSignInResponse) => {
        if (this.register && result.shouldRegister) {
          // Accessed from register page, force to ask for profession
          if (!this._registerUserService.user.professionId) {
            this._messagingService.info(
              'REGISTER.FIRST_STEP.TOAST.REGISTER_REQUIRED.TITLE',
              'REGISTER.FIRST_STEP.TOAST.REGISTER_REQUIRED.MESSAGE'
            );
          }

          this._registerUserService.updateGoogleUserInfo(this._user);
        } else if (result.shouldRegister) {
          // Accessed from login, user not registered, redirect to step 1
          this._messagingService.info(
            'REGISTER.FIRST_STEP.TOAST.REGISTER_REQUIRED.TITLE',
            'REGISTER.FIRST_STEP.TOAST.REGISTER_REQUIRED.MESSAGE'
          );
          this._registerUserService.updateGoogleUserInfo(this._user);
          this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER));
        } else if (result.shouldPay) {
          this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER_PAYMENT));
        } else if (!this._authService.isLoggedIn$.value) {
          this._messagingService.error('GOOGLE.ERROR.REGISTER.TITLE', 'GOOGLE.ERROR.REGISTER.MESSAGE');
        }
      });
    } else {
      this._messagingService.info('REGISTER.FIRST_STEP.TOAST.INFO.TITLE', 'REGISTER.FIRST_STEP.TOAST.INFO.MESSAGE');
    }
  }
}
