import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RegisterUserService } from '@scouting-public/services/register-user.service';
import { TranslateService } from '@ngx-translate/core';
import { APP_ROUTES, LANGUAGES_ENUM, STORAGE_KEYS_ENUM } from '@aft-core';

@Component({
  selector: 'aft-scout-validate-invite',
  templateUrl: './validate-invite.component.html',
  styleUrls: ['./validate-invite.component.scss'],
})
export class ValidateInviteComponent implements OnInit {
  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _registerUserService: RegisterUserService,
    private _translateService: TranslateService
  ) {}

  // Validates the token received by the URL
  ngOnInit(): void {
    const { token, lang } = this._route.snapshot.params;

    this._changeLanguage(lang);
    if (token) {
      this._registerUserService.validateInvite({ token });
    } else {
      // No token, redirect to home
      this._router.navigate(APP_ROUTES.LANDING);
    }
  }

  private _changeLanguage(lang: LANGUAGES_ENUM) {
    const languageExists = Object.values(LANGUAGES_ENUM).includes(lang);
    const newLang = languageExists ? lang : this._translateService.currentLang;

    window.localStorage.setItem(STORAGE_KEYS_ENUM.LANGUAGE, newLang);
    this._translateService.use(newLang);
  }
}
