import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { CustomValidators, FormUtils, MessagingService } from '@aft-core';

import { RegisterUserService } from '../../../../services/register-user.service';
import { generateFullPublicModuleRoute, PUBLIC_MODULE_ROUTES } from '../../../../public.routes';
import { Subscription } from 'rxjs';

@Component({
  selector: 'aft-scout-register-second-step',
  templateUrl: './register-second-step.component.html',
  styleUrls: ['./register-second-step.component.scss'],
})
export class RegisterSecondStepComponent implements OnInit, OnDestroy {
  form: FormGroup;
  private subscriptions = new Subscription();

  showPassword = false;
  showConfirmPassword = false;

  isGoogleRegister: boolean;
  isInviteRegister: boolean;

  constructor(
    private _fb: FormBuilder,
    private _router: Router,
    private _messagingService: MessagingService,
    private _registerUserService: RegisterUserService
  ) {
    this.isGoogleRegister = !!this._registerUserService.user.googleId;
    this.isInviteRegister = !!this._registerUserService.user.invitationEmailToken;
    const formData = this._registerUserService.user;

    const passwordValidators = !this.isGoogleRegister ? [Validators.required, CustomValidators.Password] : [];

    this.form = this._fb.group({
      firstName: [{ value: formData.firstName, disabled: this.isGoogleRegister }, [Validators.required, CustomValidators.Name]],
      lastName: [{ value: formData.lastName, disabled: this.isGoogleRegister }, [Validators.required, CustomValidators.Name]],
      email: [
        { value: formData.email, disabled: this.isGoogleRegister || this.isInviteRegister },
        [Validators.required, CustomValidators.Email],
      ],
      cellphone: [formData.cellphone, [Validators.required, CustomValidators.Cellphone]],
      password: [{ value: formData.password, disabled: this.isGoogleRegister }, passwordValidators],
      confirmPassword: [{ value: '', disabled: this.isGoogleRegister }, passwordValidators],
      agreeOfTerms: [false, Validators.requiredTrue],
    });
  }

  ngOnInit() {
    // Prevents from accessing the page without setting profession Id
    this.subscriptions.add(
      this._registerUserService.confirmationEmailSent$.subscribe(emailSent => {
        if (!this._registerUserService.user.professionId || emailSent) {
          this._registerUserService.resetRegistration();
        }
      })
    );
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  get firstNameCtrl() {
    return this.form.controls.firstName;
  }

  get lastNameCtrl() {
    return this.form.controls.lastName;
  }

  get emailCtrl() {
    return this.form.controls.email;
  }

  get cellphoneCtrl() {
    return this.form.controls.cellphone;
  }

  get passCtrl() {
    return this.form.controls.password;
  }

  get confirmPassCtrl() {
    return this.form.controls.confirmPassword;
  }

  saveFields(field: string) {
    this.form.controls[field].markAsDirty();
    this._registerUserService.updateUserInfo({ ...this.form.value });
  }

  // Register call
  onSubmit(): void {
    if (this.form.valid) {
      if (this.isGoogleRegister) {
        this._registerUserService.registerGoogleUser();
      } else if (this.form.value.password === this.form.value.confirmPassword) {
        this._registerUserService.registerUser();
      } else {
        this._messagingService.error('REGISTER.SECOND_STEP.TOAST.ERROR.TITLE', 'REGISTER.SECOND_STEP.TOAST.ERROR.MESSAGE');
      }
    } else {
      FormUtils.SubmitAllControls(this.form);
      this._messagingService.info('REGISTER.SECOND_STEP.TOAST.INFO.TITLE', 'REGISTER.SECOND_STEP.TOAST.INFO.MESSAGE');
    }
  }

  goBack(): void {
    this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER));
  }
}
