import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

import { ApiService, API_TYPE_ENUM, ILoginResponse, AuthService, ILoginRequest, MessagingService } from '@aft-core';
import { IGoogleLoginRequest, IGoogleSignInResponse } from '../interfaces/google';
import { RegisterUserService } from './register-user.service';
import { IRegisteringUser } from '../interfaces/register';

@Injectable()
export class LoginService {
  constructor(
    private _apiService: ApiService,
    private _authService: AuthService,
    private _registerUserService: RegisterUserService,
    private _messagingService: MessagingService
  ) {}

  // Determines if user its logged in or should register
  googleLogin(userInfo: IGoogleLoginRequest): Observable<IGoogleSignInResponse> {
    return this._apiService.post(API_TYPE_ENUM.IDENTITY, 'sessions/scoutGoogle', userInfo).pipe(
      map((response: ILoginResponse) => {
        // Log in user
        if (response?.element) {
          this._authService.loginUser(response);
          return {
            shouldRegister: false,
            shouldPay: false,
            isLogged: true,
          };
        }
        return {
          shouldRegister: false,
          shouldPay: false,
          isLogged: true,
        };
      }),
      catchError((error: HttpErrorResponse) => {
        switch (error.status) {
          case 402: // Unauthorized or user does not exist, should register
            const user: IRegisteringUser = {
              firstName: error.error.Data.userFirstName,
              lastName: error.error.Data.userLastName,
              email: error.error.Data.userEmail,
              cellphone: error.error.Data.userPhone,
              language: '',
              professionId: 0,
            };
            this._registerUserService.updateUserInfo(user);
            return of({
              shouldRegister: false,
              shouldPay: true,
              isLogged: false,
            });
          case 401: // Unauthorized or user does not exist, should register
            return of({
              shouldRegister: true,
              shouldPay: false,
              isLogged: false,
            });
          case 400: // Email and providerId do not match, should NOT register
          default:
            // Generic server error, should NOT register
            return of({
              shouldRegister: false,
              shouldPay: false,
              isLogged: false,
            });
        }
      })
    );
  }

  // Determinar si el usuario pag o no y dirigirlo a donde corresponda
  checkPayment(loginRequest: ILoginRequest): Observable<boolean | undefined> {
    return this._apiService.post(API_TYPE_ENUM.IDENTITY, 'sessions/scouting', loginRequest).pipe(
      map((response: ILoginResponse) => {
        // Log in user
        if (response?.element) {
          this._authService.loginUser(response);
          return undefined;
        }
        return false;
      }),
      catchError((error: HttpErrorResponse) => {
        // Usuario no pago = StatusCode: 402 = Message: "This user account has a pending payment" =
        // Usuario Inactivo = StatusCode: 403
        switch (error.error.StatusCode) {
          case 402: // User has pending payment, should redirect to payment
            const user: IRegisteringUser = {
              firstName: error.error.Data.userFirstName,
              lastName: error.error.Data.userLastName,
              email: error.error.Data.userEmail,
              cellphone: error.error.Data.userPhone,
              language: '',
              professionId: 0,
            };
            this._registerUserService.updateUserInfo(user);
            return of(true);
          case 403: // User is disabled, should show error message
            this._messagingService.error('LOGIN.TOAST.ERROR_INACTIVE.TITLE', 'LOGIN.TOAST.ERROR_INACTIVE.MESSAGE');
            return of(false);
          default:
            // Generic server error, should NOT login
            this._handleLoginError();
            return of(false);
        }
      })
    );
  }

  private _handleLoginError(): void {
    this._authService.isLoggedIn$.next(false);
    this._messagingService.error('LOGIN.TOAST.ERROR.TITLE', 'LOGIN.TOAST.ERROR.MESSAGE');
  }
}

