import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { SocialAuthService } from 'angularx-social-login';
import { Subscription } from 'rxjs';

import { MessagingService, CustomValidators, AuthService, FormUtils, USER_PROFESSIONS } from '@aft-core';

import { RegisterUserService } from '../../../../services/register-user.service';
import { generateFullPublicModuleRoute, PUBLIC_MODULE_ROUTES } from '../../../../public.routes';

@Component({
  selector: 'aft-scout-register-first-step',
  templateUrl: './register-first-step.component.html',
  styleUrls: ['./register-first-step.component.scss'],
})
export class RegisterFirstStepComponent implements OnInit, OnDestroy {
  form: FormGroup;
  private subscriptions = new Subscription();

  isPlatformRegister: boolean = false;
  isInviteRegister: boolean = false;

  professions = USER_PROFESSIONS;

  constructor(
    private _fb: FormBuilder,
    private _router: Router,
    private _messagingService: MessagingService,
    private _registerUserService: RegisterUserService,
    private _authService: AuthService,
    private _socialAuthService: SocialAuthService
  ) {
    this.form = this._fb.group({
      selectedProfession: [this._registerUserService.user.profession, [Validators.required, CustomValidators.EmptyDropdown]],
    });
  }

  ngOnInit(): void {
    this.subscriptions.add(
      this._registerUserService.isPlatformRegister$.subscribe((val: boolean) => (this.isPlatformRegister = val))
    );
    this.subscriptions.add(
      this._registerUserService.isInviteRegister$.subscribe((val: boolean) => (this.isInviteRegister = val))
    );

    this.form.controls.selectedProfession.setValue(this._registerUserService.user.profession);
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  onClickDropdown(): void {
    this._registerUserService.changeProfession(this.form.value.selectedProfession);
  }

  onSubmit(): void {
    if (this.form.valid) {
      this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER_SECOND_STEP));
    } else {
      FormUtils.SubmitAllControls(this.form);
      this._messagingService.info('REGISTER.FIRST_STEP.TOAST.INFO.TITLE', 'REGISTER.FIRST_STEP.TOAST.INFO.MESSAGE');
    }
  }

  goBack(): void {
    if (this.isPlatformRegister) {
      this._socialAuthService.signOut();
    }
    this._registerUserService.resetRegistration();
  }
}
