import { Component, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';

import { RegisterUserService } from '../../../../services/register-user.service';

@Component({
  selector: 'aft-scout-register-third-step',
  templateUrl: './register-third-step.component.html',
  styleUrls: ['./register-third-step.component.scss'],
})
export class RegisterThirdStepComponent implements OnDestroy {
  subscription: Subscription;

  constructor(private _registerUserService: RegisterUserService, private _router: Router) {
    this.subscription = this._registerUserService.confirmationEmailSent$.subscribe(emailSent => {
      if (!emailSent) {
        this._registerUserService.resetRegistration();
      }
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
