import { ApiService, API_TYPE_ENUM, APP_ROUTES, MessagingService } from '@aft-core';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject } from 'rxjs';
import { SocialUser } from 'angularx-social-login';

import { IRegisteringUser, IRegisterUserRequest, IUserProfession } from '../interfaces/register';
import { IValidateTokenRequest, IValidateTokenResponse } from '../interfaces/validate-token';
import { generateFullPublicModuleRoute, PUBLIC_MODULE_ROUTES } from '../public.routes';
import { IGoogleSignInRequest } from '@scouting-public/interfaces/google';
import { IValidateInviteRequest, IValidateInviteResponse } from '@scouting-public/interfaces/validate-invite';

const initialState = {
  firstName: '',
  lastName: '',
  email: '',
  cellphone: '',
  language: 'es',
  profession: { name: '', id: 0 },
  professionId: 0,
};

@Injectable()
export class RegisterUserService {
  confirmationEmailSent$ = new BehaviorSubject<boolean>(false);
  isPlatformRegister$ = new BehaviorSubject<boolean>(false);
  isInviteRegister$ = new BehaviorSubject<boolean>(false);

  private registeringUser: IRegisteringUser = { ...initialState };

  constructor(
    private _apiService: ApiService,
    private _translateService: TranslateService,
    private _router: Router,
    private _messagingService: MessagingService
  ) {}

  get user() {
    return this.registeringUser;
  }

  // Selects Profession
  changeProfession(profession: IUserProfession): void {
    this.user.profession = profession;
    this.user.professionId = profession.id;
  }

  // Resets service to initial state
  resetRegistration() {
    this.isPlatformRegister$.next(false);
    this.isInviteRegister$.next(false);
    this.registeringUser = { ...initialState };
    this._router.navigate(APP_ROUTES.LANDING);
  }

  // Updates user info
  updateUserInfo(userInfo: IRegisteringUser) {
    this.registeringUser = {
      ...this.user,
      ...userInfo,
    };

    this.isPlatformRegister$.next(!!this.registeringUser.googleId);
  }

  // Updates user info from google
  updateGoogleUserInfo(googleUser: SocialUser): void {
    this.registeringUser = {
      ...this.registeringUser,
      firstName: googleUser.firstName,
      lastName: googleUser.lastName,
      email: googleUser.email,
      photoUrl: googleUser.photoUrl,
      googleId: googleUser.idToken,
    };

    this.isPlatformRegister$.next(!!this.registeringUser.googleId);

    // If professionId its loaded, it means the register its being done from the 1 step, so redirect to 2nd step
    this._router.navigate(
      generateFullPublicModuleRoute(
        this.registeringUser.professionId ? PUBLIC_MODULE_ROUTES.REGISTER_SECOND_STEP : PUBLIC_MODULE_ROUTES.REGISTER
      )
    );
  }

  // Normal account creation
  registerUser() {
    const newUser: IRegisterUserRequest = {
      firstName: this.user.firstName,
      lastName: this.user.lastName,
      password: this.user.password,
      cellphone: this.user.cellphone,
      email: this.user.email,
      professionId: this.user.professionId,
      language: this._translateService.currentLang,
    };

    if (this.user.invitationEmailToken) {
      newUser.invitationEmailToken = this.user.invitationEmailToken;
    }

    this._apiService.post(API_TYPE_ENUM.IDENTITY, 'users/scouts', newUser).subscribe(
      () => {
        // Normal user registered
        if (!this.isInviteRegister$.value) {
          this.confirmationEmailSent$.next(true);
          this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER_THIRD_STEP));
        } else {
          // Invited user registered
          // Payment method redirection goes here depending on the invitation - TBD
          this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER_PAYMENT));
        }
      },
      () => {
        this._messagingService.error('REGISTER.TOAST.EMAIL_ERROR.TITLE', 'REGISTER.TOAST.EMAIL_ERROR.MESSAGE');
      }
    );
  }

  // Validates invite
  validateInvite(inviteRequest: IValidateInviteRequest) {
    this._apiService.get(API_TYPE_ENUM.IDENTITY, `scouts/inviteValidation/${inviteRequest.token}`).subscribe(
      (response: IValidateInviteResponse) => {
        this.registeringUser = {
          ...initialState,
          firstName: response.element.firstName,
          lastName: response.element.lastName,
          email: response.element.email,
          invitationEmailToken: inviteRequest.token,
        };
        this.isInviteRegister$.next(true);
        this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER));
      },
      () => {
        this._messagingService.error('REGISTER.TOAST.TOKEN_ERROR.TITLE', 'REGISTER.TOAST.TOKEN_ERROR.MESSAGE');
        this._router.navigate(APP_ROUTES.LANDING);
      }
    );
  }

  // Validates account creation
  validateToken(validateRequest: IValidateTokenRequest) {
    this._apiService.post(API_TYPE_ENUM.IDENTITY, 'scouts/validate', validateRequest).subscribe(
      (response: IValidateTokenResponse) => {
        this.registeringUser = {
          ...initialState,
          firstName: response.element.firstName,
          lastName: response.element.lastName,
          email: response.element.email,
          cellphone: response.element.cellphone,
        };
        this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER_PAYMENT));
      },
      () => {
        this._messagingService.error('REGISTER.TOAST.TOKEN_ERROR.TITLE', 'REGISTER.TOAST.TOKEN_ERROR.MESSAGE');
        this._router.navigate(APP_ROUTES.LANDING);
      }
    );
  }

  // Google account creation
  registerGoogleUser() {
    const newUser: IGoogleSignInRequest = {
      firstName: this.user.firstName,
      lastName: this.user.lastName,
      cellphone: this.user.cellphone,
      email: this.user.email,
      professionId: this.user.professionId,
      language: this._translateService.currentLang,
      googleId: this.user.googleId,
      googleImage: this.user.photoUrl,
    };

    return this._apiService.post(API_TYPE_ENUM.IDENTITY, 'scouts/googleSignIn', newUser).subscribe(
      () => {
        this._router.navigate(generateFullPublicModuleRoute(PUBLIC_MODULE_ROUTES.REGISTER_PAYMENT));
      },
      () => {
        this._messagingService.error('REGISTER.TOAST.EMAIL_ERROR.TITLE', 'REGISTER.TOAST.EMAIL_ERROR.MESSAGE');
      }
    );
  }
}
