import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PublicGuard } from '@aft-core';

import { PublicLayoutComponent } from '@scouting-public/components/public-layout/public-layout.component';
import { LoginComponent } from '@scouting-public-components/login/login.component';
import { ForgotComponent } from '@scouting-public-components/forgot/forgot.component';
import { ResetComponent } from '@scouting-public-components/reset/reset.component';
import { RegisterFirstStepComponent } from '@scouting-public-components/register-first-step/register-first-step.component';
import { ValidateTokenComponent } from '@scouting-public-components/validate-token/validate-token.component';
import { ValidateInviteComponent } from '@scouting-public-components/validate-invite/validate-invite.component';
import { RegisterPaymentComponent } from '@scouting-public-components/register-payment/register-payment.component';
import { RegisterThirdStepComponent } from '@scouting-public-components/register-third-step/register-third-step.component';
import { RegisterSecondStepComponent } from '@scouting-public-components/register-second-step/register-second-step.component';
import { PUBLIC_MODULE_ROUTES } from './public.routes';

const routes: Routes = [
  {
    path: PUBLIC_MODULE_ROUTES.LOGIN,
    component: PublicLayoutComponent,
    canActivate: [PublicGuard],
    children: [
      {
        path: PUBLIC_MODULE_ROUTES.LOGIN,
        component: LoginComponent,
      },
      {
        path: PUBLIC_MODULE_ROUTES.FORGOT_PASSWORD,
        component: ForgotComponent,
      },
      {
        path: PUBLIC_MODULE_ROUTES.RESET_PASSWORD,
        component: ResetComponent,
      },
      {
        path: PUBLIC_MODULE_ROUTES.REGISTER,
        component: RegisterFirstStepComponent,
      },
      {
        path: PUBLIC_MODULE_ROUTES.REGISTER_SECOND_STEP,
        component: RegisterSecondStepComponent,
      },
      {
        path: PUBLIC_MODULE_ROUTES.REGISTER_THIRD_STEP,
        component: RegisterThirdStepComponent,
      },
      {
        path: PUBLIC_MODULE_ROUTES.REGISTER_VALIDATE_TOKEN,
        component: ValidateTokenComponent,
      },
      {
        path: PUBLIC_MODULE_ROUTES.REGISTER_VALIDATE_INVITE,
        component: ValidateInviteComponent,
      },
      {
        path: PUBLIC_MODULE_ROUTES.REGISTER_PAYMENT,
        component: RegisterPaymentComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PublicRoutingModule {}
